import React, { useState, useEffect } from 'react';
import LocationAutocomplete from './LocationAutocomplete';
import { categoryColors } from './utils/categoryColors';

export default function AddNewActivityModal({
  isOpen,
  onClose,
  onAddActivity,
  initialActivity = null
}) {
  const [activity, setActivity] = useState({
    name: '',
    description: '',
    time: '',
    category: '',
    location: '',
    lat: null,
    lng: null,
  });

  useEffect(() => {
    if (initialActivity) {
      setActivity(initialActivity);
    } else {
      setActivity({
        name: '',
        description: '',
        time: '',
        category: '',
        location: '',
        lat: null,
        lng: null,
      });
    }
  }, [initialActivity, isOpen]);

  if (!isOpen) return null;

  const handleChange = (e) => {
    const { name, value } = e.target;
    setActivity((p) => ({ ...p, [name]: value }));
  };

  const handlePlaceSelect = (place) => {
    setActivity((p) => ({
      ...p,
      location: place.address || place.name || '',
      lat: place.lat,
      lng: place.lng,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!activity.name || !activity.location) {
      return alert('Please provide activity name and location.');
    }
    onAddActivity({
      ...activity,
      id: activity.id || crypto.randomUUID?.() || `a${Date.now()}`,
    });
    onClose();
  };

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm p-4">
      <div className="bg-white rounded-2xl shadow-2xl w-full max-w-lg overflow-hidden">
        <div className="px-6 py-4 flex items-center justify-between border-b">
          <h3 className="text-lg font-semibold">Add Activity</h3>
          <button onClick={onClose} className="text-gray-600 hover:text-gray-900"></button>
        </div>

        <form className="px-6 py-6 space-y-4" onSubmit={handleSubmit}>
          <div>
            <label className="block text-sm font-medium text-gray-700">Name *</label>
            <input
              name="name"
              value={activity.name}
              onChange={handleChange}
              className="w-full mt-1 px-3 py-2 rounded-lg bg-gray-100 border border-gray-300"
              required
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700">Description</label>
            <textarea
              name="description"
              value={activity.description}
              onChange={handleChange}
              rows={2}
              className="w-full mt-1 px-3 py-2 rounded-lg bg-gray-100 border border-gray-300 resize-none"
            />
          </div>

          <div className="flex gap-4">
            <div className="flex-1">
              <label className="block text-sm font-medium text-gray-700">Time</label>
              <input
                name="time"
                type="time"
                value={activity.time}
                onChange={handleChange}
                className="w-full mt-1 px-3 py-2 rounded-lg bg-gray-100 border border-gray-300"
              />
            </div>

            <div className="flex-1">
              <label className="block text-sm font-medium text-gray-700">Category</label>
              <select
                name="category"
                value={activity.category}
                onChange={handleChange}
                className="w-full mt-1 px-3 py-2 rounded-lg bg-gray-100 border border-gray-300"
              >
                <option value="">Select</option>
                {Object.keys(categoryColors).map((k) => (
                  <option key={k} value={k}>
                    {categoryColors[k].label || k}
                  </option>
                ))}
              </select>
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700">Location *</label>
            <div className="mt-1">
              <LocationAutocomplete onPlaceSelected={handlePlaceSelect} defaultValue={activity.location} />
            </div>
            {activity.location && (
              <div className="flex items-center gap-2 mt-2">
                <span className="text-sm text-gray-600">{activity.location}</span>
                {activity.lat && activity.lng && <span className="text-xs text-gray-400"> {activity.lat.toFixed(4)},{activity.lng.toFixed(4)}</span>}
              </div>
            )}
          </div>

          <div className="flex justify-end gap-3 pt-2">
            <button type="button" onClick={onClose} className="px-4 py-2 bg-gray-200 rounded-lg">Cancel</button>
            <button type="submit" className="px-4 py-2 bg-black text-white rounded-lg">Add</button>
          </div>
        </form>
      </div>
    </div>
  );
}
