import React, { useState } from "react";
import { Link } from "react-router-dom";
import { FiEdit2, FiShare2, FiDownload, FiPlus } from "react-icons/fi";
import AddNewActivityModal from "./AddNewActivityModal";

export default function ItineraryPage() {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleAddActivity = (activity) => {
    // For now, just log the new activity; later this should update state
    console.log('New activity added:', activity);
    setIsModalOpen(false);
  };

  return (
    <div className="relative w-full min-h-screen bg-gray-100">

      {/* Background */}
      <img
        src="/home.jpg"
        alt="background"
        className="absolute inset-0 w-full h-full object-cover brightness-75"
      />
      <div className="absolute inset-0 bg-black/30"></div>

      {/* Content Wrapper */}
      <div className="relative z-10 max-w-6xl mx-auto py-10 px-4">

        {/* Navbar */}
<nav className="fixed top-0 left-0 w-full z-50">
  <div className="max-w-15xl mx-auto px-6 py-4 flex items-center justify-between
                  bg-white/10 backdrop-blur-lg rounded-b-3xl shadow-lg border border-white/20">

    {/* Logo + Title */}
    <div className="flex items-center gap-4">
      <Link to="/" className="flex items-center gap-3">
        <img
          src="/logo.avif"
          alt="Logo"
          className="w-12 h-12 rounded-full border-2 border-white shadow-md"
        />
        <h1 className="text-xl md:text-2xl font-bold text-white tracking-wide">
          Travel Itinerary Planner
        </h1>
      </Link>
    </div>

    {/* Nav Buttons */}
                    <div className="flex items-center gap-4 text-white">

                      <Link
                        to="/"
                        className="px-4 py-2 rounded-xl border border-white/50 hover:bg-white hover:text-gray-900
                                  transition-all shadow-md backdrop-blur-sm"
                      >
                        Home
                      </Link>

                      <Link
                        to="/register"
                        className="px-4 py-2 rounded-xl border border-white/50 hover:bg-white hover:text-gray-900
                                  transition-all shadow-md backdrop-blur-sm"
                      >
                        Hello User
                      </Link>

                      <Link
                        to="/"
                        className="px-4 py-2 rounded-xl border border-red-300 hover:bg-red-500 hover:text-white
                                  hover:border-red-500 transition-all shadow-md backdrop-blur-sm"
                      >
                        Logout
                      </Link>

                    </div>
                  </div>
                </nav>

        {/* Trip Summary Bar */}
        <div className="bg-white/90 backdrop-blur-md p-5 rounded-2xl shadow-lg flex justify-between items-center mb-8 mt-24">
          <div>
            <h2 className="text-xl md:text-2xl font-semibold">Nepal</h2>
            <p className="text-sm md:text-base text-gray-700">2 days, 1 activity</p>
          </div>

            <div className="flex items-center gap-3">
            <button className="px-4 py-2 bg-white rounded-xl flex items-center gap-2 shadow-md hover:bg-gray-100 transition">
              <FiShare2 /> Share
            </button>
            <button className="px-4 py-2 bg-white rounded-xl flex items-center gap-2 shadow-md hover:bg-gray-100 transition">
              <FiDownload /> Export
            </button>
            <button onClick={() => setIsModalOpen(true)} className="px-4 py-2 bg-green-600 text-white rounded-xl flex items-center gap-2 shadow-md hover:bg-green-700 transition">
              <FiPlus /> Add Activity
            </button>
          </div>
        </div>

        {/* Main Layout */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">

          {/* Left Content: Days & activities */}
          <div className="lg:col-span-2 flex flex-col gap-6">

            {/* Day Card */}
            <div className="bg-white/90 backdrop-blur-xl rounded-3xl p-6 shadow-lg hover:shadow-2xl transition">
              <div className="flex justify-between items-center mb-3">
                <h3 className="text-xl md:text-2xl font-semibold">Day 1, Day 2</h3>
                <button className="text-sm md:text-base text-blue-600 hover:underline">
                  + Add day
                </button>
              </div>

              <p className="text-gray-700 font-medium mb-6">
                Saturday, November 15, 2025
              </p>

              {/* Activity List */}
              <div className="space-y-4">
                <div className="bg-white rounded-2xl p-4 shadow-md flex justify-between items-start hover:shadow-lg transition">
                  <div>
                    <p className="font-semibold text-gray-800">Check-in at Hotel</p>
                    <p className="text-sm text-gray-600">
                      Arrival and hotel check-in <br />
                      <span className="font-medium">10:00</span>
                    </p>
                    <p className="text-sm text-gray-700 mt-1">
                      Downtown Hotel
                    </p>
                  </div>

                  <button className="text-gray-600 hover:text-black transition">
                    <FiEdit2 size={18} />
                  </button>
                </div>
              </div>

              {/* Add Activity Button */}
              <button 
              onClick={() => setIsModalOpen(true)}
              className="w-full mt-6 py-2 bg-blue-600 text-white rounded-2xl border border-blue-600 hover:bg-blue-700 transition font-medium">
                + Add Activity
              </button>
            </div>
          </div>

          {/* Right Content: Map */}
          <div className="bg-white/90 backdrop-blur-xl rounded-3xl p-6 shadow-lg">
            <h3 className="font-semibold text-xl mb-4">Map View</h3>

            <div className="rounded-2xl overflow-hidden mb-5">
              <img
                src="/map.jpg"
                alt="Map"
                className="w-full h-56 object-cover"
              />
            </div>

            <h4 className="font-medium text-gray-800 mb-2">Locations</h4>
            <ul className="text-sm text-gray-700 space-y-1">
              <li>Location 1</li>
              <li>Location 2</li>
            </ul>
          </div>

        </div>
        {/* Add Activity Modal */}
        <AddNewActivityModal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)} onAddActivity={handleAddActivity} />
      </div>
    </div>
  );
}
